body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

button {
  font-size: 20px;
}

.Error {
  color: red;
}
.CoinFlipDemo .Result {
  width: 100px;
  height: 100px;
  text-align: center;
  margin: 0 auto;
  padding-bottom: 20px;
}

.CoinFlipDemo .Result img {
  width: 100px;
  height: 100px;
}
.RollDiceDemo .Result {
  width: 250px;
  height: 70px;
  text-align: center;
  margin: 0 auto;
  padding-bottom: 20px;
}

.RollDiceDemo .Result img {
  width: 70px;
  height: 70px;
}
